import React from "react";
import { ICommonRenderProps } from "../../types/common";
import { Layout } from "../Layout";
import $style from "./style.module.less";

export const Block: React.FC<ICommonRenderProps> = ({ project }) => {
  const style = project.style;

  const newStyle = {
    background: `url(${style.background})`,
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
  };
  const childrenEl = project.children || [];

  return (
    <div className={$style.wrapper} style={newStyle}>
      {childrenEl.map((item) => (
        // eslint-disable-next-line react/jsx-key
        <Layout project={item} key={item.id} />
      ))}
    </div>
  );
};
